___CSS_LOADER_EXPORT___.push([module.id, `.login-form {
  margin: auto;
  width: 700px;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
:focus {
  outline: none;
}
.input-box {
  position: relative;
  display: block;
  margin-bottom: 25px;
  display: flex;
  align-items: flex-start;
}
.input {
  font-family: Cousine;
  font-weight: 400;
  font-size: 22px;
  color: #545454;
  width: 350px;
  height: 32px;
  border-radius: 8px;
  letter-spacing: 1px;
  padding: 7px 15px;
  border: 1px solid #6d3c55;
  position: relative;
  background: transparent;
}

.input ~ .focus-bg {
  font-family: Cousine;
  font-weight: 400;
  font-size: 14px;
  color: #545454;
  position: absolute;
  left: 0;
  top: 0;
  width: 382px;
  height: 100%;
  background-color: #f4f3f3;
  opacity: 0;
  transition: 0.5s;
  z-index: -1;
}
.input:focus ~ .focus-bg {
  transition: 0.5s;
  opacity: 1;
}
.login-label {
  font-family: Cousine;
  font-weight: 400;
  font-size: 20px;
  color: #6d3c55;
}
.input-btn {
  margin: 0 auto;
}
.input-message {
  width: 250px;
  font-family: Cousine;
  font-weight: 500;
  font-size: 14px;
  color: red;
  margin: 0 0 0 15px;
  padding: 0;
}
